	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nocturnidades","author":"Enrique Moriarty"}},"markdownRemark":{"id":"/Users/enric/Workspace/personal/nocturnidades-blog/nocturnidades/src/pages/articulos/la-gratuidad-tecnologica.md absPath of file >>> MarkdownRemark","html":"<p>Recientemente he acudido a una convención sobre desarrollo web. Sí, una de esas llenas de frikazos y que no paran de hacer bromas con lindos gatitos y perritos. Pero no os dejéis engañar por el aspecto, debajo de esa carcasa se esconde gente interesante e interesada. Porque seamos sinceros ¿Quién patrocina este tipos de eventos? Bien, hay un organizador detrás con sus propios intereses personales (y tanto como que se comió mi entrada y no pude entrar al taller pero eso es otra historia). También están las compañías con intereses en vender su producto entre los informáticos. En esta ocasión fue el turno de <em>Webpack</em>, <em>Nest.js</em> y <em>Vue.js</em>. Pero si son gratuitos exclamarán algunos. Por supuesto, como tantas otras tecnologías y herramientas. Ahora, por un momento ¿Nunca has pensado cual es el interés tras su gratuidad?</p>\n<p>Es obvio que de estas convenciones se extraen un buen pico. Mi empresa compró el pase completo que constituía en un tallar el primer día más los dos siguientes de conferencia. Costó 600€ y eso que había una supuesta rebaja de 90€. Solo las conferencias costaban 400€.</p>","frontmatter":{"title":"La gratuidad tecnológica","date":"July 20, 2018"}}},"pathContext":{"slug":"/articulos/la-gratuidad-tecnologica/","previous":{"fields":{"slug":"/relatos/huracan-purificador/"},"frontmatter":{"title":"Huracán purificador"}},"next":null}}

